@import url('https://cdn.jsdelivr.net/npm/bootstrap@5.2.2/dist/css/bootstrap.min.css');

.avatar {
    display: inline-flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    background-color: #006eff;
    border-radius: 50%;
    color: #fff;
    overflow: hidden;
}

.chat {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.chat__name {
    word-wrap: break-word;
}

.progress-bar {
    height: 25px;
    padding: 1px;
    border: 1px solid #000;
}

.progress-bar__line {
    background-color: #006eff;
    height: 100%;
}